<template>
	<view>
		<view>
			<view class="example-body">
				<uni-nav-bar :fixed="true" color="#333333" background-color="#FFFFFF" @clickRight="handleSearchOpen">
					<!-- <view slot="left" @click="handle_back"></view> -->
					<navigator slot="left"  animation-type="pop-out" animation-duration="300" open-type="navigateBack" >
						<text  class="iconfont iconfanhui f18"></text>
					</navigator>
					<view slot="left"><text class="iconfont iconfengexian f18 "></text></view>
					<view slot="left" @click="handle_plat"><text class="iconfont iconguanbi f18 pl8"></text></view>
					<view class="input-view f18 uni_3030">平城热线</view>
					<!-- <block slot="right"><text class="iconfont iconsousuo f22"></text></block> -->
				</uni-nav-bar>
			</view>
		</view>
		<view class="uni_detail ">
			<view class="uni-divider-line mb20">
				<view class="uni-flex-list">
					<text class="detail_name">{{ handler_hide(detailDate.submitterName) }}</text>
					 <view>
						<!-- <image v-if="detailDate.isFollow == '1'" src="../static/images/heart_default.png" mode="aspectFill" class="list_follow uni-vertical-align"></image>
						<image v-if="detailDate.isFollow == '2'" src="../static/images/heart.png" mode="aspectFill" class="list_follow uni-vertical-align"></image>
						<text class="pl8 f14 name_color">{{ detailDate.isFollowDesc }}</text> -->
					</view> 
				</view>
				<view class="mb15">
					<text class="name_color">诉求编号：</text>
					<text class="name_color">{{ detailDate.networkId }}</text>
				</view>
			</view>
			<view>
				<text class="uni_c33 fontw6 f16"  v-html="ruleTitle(detailDate.networkTitle)">
				</text>
				<view class="f16 uni_c33 mb15 mt15" v-html="ruleTitle(detailDate.networkContent)"></view>
				<view class="uni-flex-list_img">
					<view class="img" v-for="(itemson, index) in detailDate.networkFile">
						<image :src="imgPath + itemson" alt="" class="uni_width uni_height" mode="aspectFill" @error="handle_image_error(index)" @tap="previewImage"></image>
					</view>
				</view>
				<text class="f12 uni_c93">{{ detailDate.submitTime }}</text>
			</view>
			<view class="detail_notice" v-if="detailDate.isOffice == 1">
				<view class="uni-flex-list mb15">
					<text class="replayTip f14 fontw6">平城热线回复：</text>
					<text class="f12 uni_c93">{{ detailDate.workNetworkOfficeResp.officeTime }}</text>
				</view>
				<text class="f14 detail_reply">{{ detailDate.workNetworkOfficeResp.networkOfficeContent }}</text>
				<view class="mt20 f12 detail_company">
					<text>处办单位：</text>
					<text>{{ detailDate.workNetworkOfficeResp.officeUnit }}</text>
				</view>
			</view>
		</view>
	</view>
</template>

<script>
export default {
	components: {},
	data() {
		return {
			imgPath: this.$config.imgPath,
			detailDate: {},
			networkId: '',
			imageError: false,
			imageList: [],
			imageError: require('../static/images/error.png'),
			keyword_show: ''
		};
	},
	/*S 监听页面滚动*/
	onPageScroll(e){
		console.log(e)
	},
	/*E 监听页面滚动*/
	onLoad(options) {
		let id = options.id;
		let submitterId = options.submitterId;
		this.networkId = id;
		if (id) {
			this.loadData(id, submitterId);
		}
	},
	onShow() {
		this.keyword_show = uni.getStorageSync('keyword');
	},
	methods: {
		/*S 预览*/
		previewImage: function(e) {
			var current = e.target.dataset.src
			var array = [];
			this.detailDate.networkFile.map(i=>{
				array.push('/'+i)
			})
			uni.previewImage({
				current: current,
				urls: array
			})
		},
		/*E 预览*/
		/*S 文字高亮*/
		ruleTitle(items) {
			if(items){
				this.keyword = uni.getStorageSync('keyword');
				let title = items;
				let rep = new RegExp(this.keyword, 'g');
				let resDtring = `<span style='color:#4A93FE;'>${this.keyword}</span>`;
				return title.replace(rep, resDtring);
			}
		},
		/*E 文字高亮*/
		/*S 返回*/
		handle_back() {
			uni.navigateBack()
		},
		/*E 返回*/
		/*S 导航 搜索按钮 */
		handleSearchOpen() {
			uni.navigateTo({
				url: '/pages/searchAttention/index'
			});
		},
		/*E导航 搜索按钮 */
		/*S 关闭按钮*/
		handle_close() {
			// window.location = 'flutter//goBack'
			if (uni.getSystemInfoSync().platform == 'ios') {
				window.location = 'ios://details';
			} else {
				window.android.finish();
			}
		},
		handle_plat() {
			var _this = this;
			uni.showModal({
				title: '温馨提示',
				content: '是否关闭该应用',
				success: function(res) {
					if (res.confirm) {
						_this.handle_close();
uni.removeStorageSync('keyword')
					} else if (res.cancel) {
						console.log('用户点击取消');
					}
				}
			});
		},
		/*e 关闭按钮*/
		//带下拉刷新和上滑加载
		loadData(id, submitterId) {
			this.$minApi.networkWorkOrderGet({ networkId: id, followUser: submitterId }).then(res => {
				var keyword = uni.getStorageSync('keyword');
				if (res.data.networkFile) {
					res.data.networkFile = JSON.parse(res.data.networkFile);
				}

				this.detailDate = res.data;
				if (res.data.networkContent == keyword || res.data.networkTitle == keyword) {
					this.detailDate.keyword_show = true;
					let index = res.data.networkContent.indexOf(keyword)
					console.log(index)
				}
				
			});
		},
		handle_image_error: function(index) {
			// this.$set(this.imageList, index, this.imageError);
		},
		/*S 取消关注 */
		handleFollow() {
			console.log(this.networkId);
			var followlist = {
				networkId: this.networkId,
				userId: item.submitterId,
				deviceId: '',
				sessionId: ''
			};
			if (this.detailDate.isFollow == '2') {
				// 未关注
				this.$minApi.networkWorkOrderCancelFollow({ ...followlist }).then(res => {
					if (res.code == 0) {
						uni.showToast({
							title: res.msg,
							duration: 2000
						});
						this.loadData(this.networkId);
					} else {
						uni.showToast({
							title: res.msg,
							duration: 2000,
							icon: 'none'
						});
						this.loadData(this.networkId);
					}
				});
			} else {
				// 已关注
				this.$minApi.networkWorkOrderFollow({ ...followlist }).then(res => {
					if (res.code == 0) {
						uni.showToast({
							title: res.msg,
							duration: 2000
						});
						this.loadData(this.networkId);
					} else {
						uni.showToast({
							title: res.msg,
							duration: 2000,
							icon: 'none'
						});
						this.loadData(this.networkId);
					}
				});
			}
		},
		/*E 取消关注*/
		/*S 隐藏字符*/
		handler_hide(string) {
			if (string) {
				let arrBox = [...string];
				var str = '';
				arrBox.map((res, index) => {
					if (index > 0) {
						str += '*';
					} else {
						str += res;
					}
				});
				return str;
			}
		}
		/*E 隐藏字符*/
	}
};
</script>

<style lang="scss">
.uni_detail {
	padding: 30upx;
	.list_follow {
		width: 28upx;
		height: 28upx;
	}
	.detail_name {
		font-size: 32upx;
		color: #333333;
	}
	.detail_notice {
		background-color: #f5f9ff;
		padding: 30upx;
		margin-top: 40upx;
	}
	.replayTip {
		color: #3893fe;
	}
	.detail_reply {
		color: #414141;
	}
	.detail_company {
		color: #404040;
	}
	.uni-flex-list_img {
		display: flex;
		justify-content: flex-start;
		flex-wrap: wrap;
		align-items: center;
	}
	.img {
		// border:2upx solid #000000;
		margin-bottom: 10upx;
		margin-right: 5upx;
	}
	.img:nth-child(3n) {
		margin-right: -2upx;
	}
	.imageError {
		background: #f5f7fa;
		display: flex;
		justify-content: center;
		align-items: center;
		font-size: 14px;
		color: #c0c4cc;
		vertical-align: middle;
	}
}
.keyword_active{
		color: #4A93FE !important;
}
</style>
